import React, { useState } from "react";
import { Link } from "react-router-dom";

import AddToCartBtnBg from "../../../assets/icons/AddToCartBtnBg";
import DottedLine from "../../../assets/icons/DottedLine";

import styles from "./ProductInfo.module.sass";

const ProdictInfo = () => {
  const [selectedSize, setSelectedSize] = useState("XSS");

  const handleSizeChange = (e) => {
    setSelectedSize(e.target.value);
  };
  return (
    <div className={styles.productInfo}>
      <h1 className={styles.name}> Gone Crazy </h1>
      <p className={styles.price}>2 499 &#8381;</p>
      <div className={styles.size}>
        <p className={styles.chooseSize}> :</p>
        <form action="" className={styles.sizeOptions}>
          <label for="XSS">
            <input
              type="radio"
              id="XSS"
              name="size"
              value="XSS"
              checked={selectedSize === "XSS"}
              onChange={handleSizeChange}
            />
            <span>XS S</span>
          </label>
          <label for="SM">
            <input
              type="radio"
              id="SM"
              name="size"
              value="SM"
              checked={selectedSize === "SM"}
              onChange={handleSizeChange}
            />
            <span>S M</span>
          </label>
          <label for="LXL">
            <input
              type="radio"
              id="LXL"
              name="size"
              value="LXL"
              checked={selectedSize === "LXL"}
              onChange={handleSizeChange}
            />
            <span>L XL</span>
          </label>
          <label for="ML">
            <input
              type="radio"
              id="ML"
              name="size"
              value="ML"
              checked={selectedSize === "ML"}
              onChange={handleSizeChange}
            />
            <span>M L</span>
          </label>
        </form>
      </div>
      <Link to="/cart" className={styles.svgButton}>
        <AddToCartBtnBg />
        <span className={styles.buttonText}> </span>
      </Link>
      <p className={styles.description}>
        Over size        . 
           .    
           ,  .
      </p>
      <div className={styles.icon}>
        <DottedLine />
      </div>
      <p className={styles.madeOf}>
        : &nbsp; <span>50% , 50% </span>
      </p>
    </div>
  );
};

export default ProdictInfo;
