import React, { useState } from "react";
import { Link } from "react-router-dom";

import styles from "./Filter.module.sass";

const Filter = () => {
  const [selectedPrice, setSelectedPrice] = useState("allprices");
  const [selectedSize, setSelectedSize] = useState("XSS");

  const handlePriceChange = (e) => {
    setSelectedPrice(e.target.value);
  };

  const handleSizeChange = (e) => {
    setSelectedSize(e.target.value);
  };

  return (
    <div className={styles.filter}>
      <div className={styles.column}>
        <div className={styles.title}>:</div>
        <ul className={styles.list}>
          <li className={styles.item}>
            <Link to={"catalog"}></Link>
          </li>
          <li className={styles.item}>
            <Link to={"catalog"}></Link>
          </li>
          <li className={styles.item}>
            <Link to={"catalog"}></Link>
          </li>
          <li className={styles.item}>
            <Link to={"catalog"}></Link>
          </li>
          <li className={styles.item}>
            <Link to={"catalog"}></Link>
          </li>
        </ul>
      </div>
      <div className={styles.column}>
        <div className={styles.title}>:</div>
        <form action="" className={styles.options}>
          <div className={styles.option}>
            <input
              type="radio"
              id="allprices"
              name="price"
              value="allprices"
              checked={selectedPrice === "allprices"}
              onChange={handlePriceChange}
            />
            <label htmlFor="allprices"> </label>
          </div>
          <div className={styles.option}>
            <input
              type="radio"
              id="upto500"
              name="price"
              value="upto500"
              checked={selectedPrice === "upto500"}
              onChange={handlePriceChange}
            />
            <label htmlFor="upto500"> 500</label>
          </div>
          <div className={styles.option}>
            <input
              type="radio"
              id="from500to1000"
              name="price"
              value="from500to1000"
              checked={selectedPrice === "from500to1000"}
              onChange={handlePriceChange}
            />
            <label htmlFor="from500to1000">500  1000</label>
          </div>
          <div className={styles.option}>
            <input
              type="radio"
              id="from1000to1500"
              name="price"
              value="from1000to1500"
              checked={selectedPrice === "from1000to1500"}
              onChange={handlePriceChange}
            />
            <label htmlFor="from1000to1500">1000  1500</label>
          </div>
          <div className={styles.option}>
            <input
              type="radio"
              id="from1500"
              name="price"
              value="from1500"
              checked={selectedPrice === "from1500"}
              onChange={handlePriceChange}
            />
            <label htmlFor="from1500"> 1500</label>
          </div>
        </form>
      </div>
      <div className={styles.column}>
        <div className={styles.title}>:</div>
        <form action="" className={styles.options}>
          <div className={styles.option}>
            <input
              type="radio"
              id="XSS"
              name="size"
              value="XSS"
              checked={selectedSize === "XSS"}
              onChange={handleSizeChange}
            />
            <label htmlFor="XSS">XS  S</label>
          </div>
          <div className={styles.option}>
            <input
              type="radio"
              id="SM"
              name="size"
              value="SM"
              checked={selectedSize === "SM"}
              onChange={handleSizeChange}
            />
            <label htmlFor="SM">S  M</label>
          </div>
          <div className={styles.option}>
            <input
              type="radio"
              id="ML"
              name="size"
              value="ML"
              checked={selectedSize === "ML"}
              onChange={handleSizeChange}
            />
            <label htmlFor="ML">M  L</label>
          </div>
          <div className={styles.option}>
            <input
              type="radio"
              id="LXL"
              name="size"
              value="LXL"
              checked={selectedSize === "LXL"}
              onChange={handleSizeChange}
            />
            <label htmlFor="LXL">L  XL</label>
          </div>
        </form>
      </div>
    </div>
  );
};

export default Filter;
